
.photoIndex > h2 {
    text-transform: uppercase;
}
.description > p {
    margin: 0;
}
@media screen and (min-width:600px) {
.photoIndex {
    width: 100%;
    height: 92vh;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    text-align: center;
    padding: 40px 22%;
    box-sizing: border-box;
}
.photoIndex > h2 {
    font-size: 2rem;
    margin-bottom: 3rem;
}
.description {
    text-align: left;
    margin-bottom: 30px;
}
.description > p {
    font-size: 1.4rem;
    font-weight: 100;
}
.photoIndex > img,
  .photoIndex > .video-wrap {
    width: 100%;
    margin: 0 auto 10px;
}
}
@media screen and (max-width:600px) {
.photoIndex {
    width: 100%;
    height: 92vh;
    overflow: scroll;
    -webkit-overflow-scrolling: touch;
    text-align: center;
    padding: 20px 10%;
    box-sizing: border-box;
}
.description {
    text-align: left;
    margin-bottom: 20px;
}
.description > h3 {
    margin: 0;
}
.photoIndex > img,
  .photoIndex > .video-wrap {
    width: 100%;
    margin: 0 auto 10px;
}
}
..video-js {
  background-color: #fff !important;
}
.video-wrap, .video-player, .video-js {
  width: 100%;
  position: relative;
  padding-bottom: 56.25% !important;
  height: 0;
}
.video-player > div {
  padding-bottom: 56.25% !important;
}
